<template>
  <div class="map-view">
    <div
      ref="mapContainer"
      class="main"
    />
  </div>
</template>

<script>
import * as echarts from 'echarts'
import geoJson from '@/assets/json/map/china.json'

export default {
  mounted() {
    this.$nextTick(() => {
      this.initECharts()
    })
  },
  methods: {
    initECharts() {
      const { mapContainer } = this.$refs
      const myChart = echarts.init(mapContainer)

      // 
      echarts.registerMap('china', geoJson)

      // 
      myChart.setOption({
        // 
        backgroundColor: 'rgb(0,0,0,10%)',
        // ()
        geo: {
          map: 'china',
          // 
          aspectScale: 0.75,
          // 
          zoom: 1.1,
          // 
          itemStyle: {
            // 
            // 
            areaColor: {
              type: 'radial',
              x: 0.5,
              y: 0.5,
              r: 0.8,
              // 
              colorStops: [
                {
                  offset: 0,
                  color: '#09132c',
                },
                {
                  offset: 1,
                  color: '#274d68',
                },
              ],
              // 
              globalCoord: true,
            },
            // 
            shadowColor: 'rgb(58,115,192)',
            // 
            shadowOffsetX: 5,
            shadowOffsetY: 7,
          },
          region: [
            {
              name: '',
              itemStyle: {
                opacity: 0,
              },
            },
          ],
        },
        series: [
          // 
          {
            type: 'map',
            label: {
              // 
              color: 'white',
              fontSize: 9, // 
              show: true, // 
            },
            // 
            zoom: 1.1,
            map: 'china',
            itemStyle: {
              // 
              backgroundColor: 'rgb(147,235,248)',
              // 
              borderWidth: 1,
              // 
              areaColor: {
                color: 'rgb(70,70,150)', // 
                borderWidth: 0.1,
                type: 'radial',
                x: 0.5,
                y: 0.5,
                // 
                r: 0.8,
                colorStops: [
                  {
                    offset: 0,
                    color: 'rgb(50,50,150)',
                  },
                  {
                    offset: 1,
                    color: 'rgb(90,158,162)',
                  }, // 
                ],
                // 
                globalCoord: true,
              },
            },
            // 
            emphasis: {
              label: {
                color: 'red', // 
              },
            },
          },
        ],
      })
    },
  },
}
</script>

<style lang="scss" scoped>
.map-view {
  width: 683px;
  height: 420px;
  scale: 2.6;
}
.main {
  width: 683px;
  height: 420px;
}
</style>
